  /*
  
  table 
  
  */
 
*,
*:before,
*:after {
  box-sizing: border-box;
}


h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

.container__table {
  max-width: 90%;
  margin-right: auto;
  margin-left: auto;

  min-height: 60vh;
}

.table {
  width: 100%;
  border: 1px solid #eeeeee;
}

.table-header {
  display: flex;
  width: 100%;
  background: #512da8;
  padding: 18px 0;
}

.table-row {
  display: flex;
  width: 100%;
  padding: 18px 0;
  cursor: pointer;
  transition: 500ms ease-in-out;
}

.table-row:hover{
	background-color: black;
}

.table-row:hover .table-data{
	color: white;
}

.filter__link--number{
	font-size: 25px;
	font-weight: 900;
}

.table-data,
.header__item {
  flex: 1 1 20%;
  text-align: center;
  color: black;
  font-size: 20px;
  font-weight: 500;
}

.header__item {
  text-transform: uppercase;
}



.filter__link {
  color: white;
  text-decoration: none;
  position: relative;
  display: inline-block;
  font-size: 25px;
  padding-left: 24px;
  font-weight: 900;
  padding-right: 24px;
}
.filter__link::after {
  content: "";
  position: absolute;
  right: -18px;
  color: white;
  font-size: 12px;
  top: 50%;
  transform: translateY(-50%);
}
.filter__link.desc::after {
  content: "(desc)";
}
.filter__link.asc::after {
  content: "(asc)";
}

  /*
  
  activity
  
  */
  [x-cloak] {
	display: none;
}

[type="checkbox"] {
	box-sizing: border-box;
	padding: 0;
}

.form-checkbox {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	-webkit-print-color-adjust: exact;
	color-adjust: exact;
	display: inline-block;
	vertical-align: middle;
	background-origin: border-box;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	flex-shrink: 0;
	color: currentColor;
	background-color: #fff;
	border-color: #e2e8f0;
	border-width: 1px;
	border-radius: 0.25rem;
	height: 1.2em;
	width: 1.2em;
